import React from 'react';
import Post from './Post/Post';
import styles from './Posts.module.css';
import repass from '../../repass';
import { Link } from 'react-router-dom';

class Posts extends React.Component{
    
    state = {
        posts: []
    }
    fetchPosts = async() => {
        try {
            const { sub, user } = this.props;
            let link;
            if (user) {
                link=`u/${user}`;
            } else {
                link = `r/${sub}`;
            }
            const res = await repass.get(link);
            const posts = res.data.listings;
            if (posts) {
                await this.setState({ posts });
            }
        } catch(err) {
            console.log(err);
            console.log(err.response);
        }
    }

    async componentDidMount() {
        await this.fetchPosts();
    }
    render(){
        const { sub, user } = this.props;
        return (
            <div className={styles.posts}>
                {this.state.posts.length ? 
                this.state.posts.map(post => {
                    return <Post key={post} sub={sub} id={post}/> 
                    // note: sub has to change for user
                }) : <div className={styles.posts}>
                        <p>No Posts here!</p>
                        <Link to={`/r/${sub}/create/post`} className={styles.firstpost}>
                            Create first post
                        </Link>
                    </div>
                }
            </div>
        );
    }
}

export default Posts;